import { IUIState } from "../interfaces/interfaces"
import { types } from "../types/types"

const init = {
    modalTramiteOpen: false
}

export const uiReducer = (state:IUIState = init , action: any) => {
    switch (action.type) {
        case types.uiOpenTramiteModal:
            return{
                ...state,
                modalTramiteOpen: true
            }
        case types.uiCloseTramiteModal:
            return{
                ...state,
                modalTramiteOpen: false
            }
        default:
            return state
    }
}