import Swal from 'sweetalert2';
import { fetchConToken, fetchSinToken } from '../helpers/fetch';
import { LoginUser, LoginValues, types } from '../types/types';
import { tramiteLogout } from './tramites';


//================================================================//
export const startLogin = (values:LoginValues) => {
    return async(dispatch: (arg0: { type: types; payload: LoginUser; }) => void)=>{
        const resp = await fetchSinToken('auth', values, 'POST')
        const body = await resp.json()
        if(body.ok) {
            localStorage.setItem('token', body.token)
            localStorage.setItem('token-init-date', new Date().getTime().toString())
            dispatch(login({
                uid:body.uid,
                name:body.name,
            }))
        }else{
            if(body.errors){
                Object.entries(body.errors).map(error => {
                    return error.map((err:any) => {
                        return Swal.fire('Error', err.msg, 'error')
                    })
                })
            }else{
                Swal.fire('Error', body.msg, 'error')
            }
        }
    }
}
const login = (user:LoginUser) => ({
    type: types.authLogin,
    payload:user
})


//================================================================//
export const startChecking = () => {
    return async(dispatch: (arg0: { type: types; }) => void)=>{
        const resp = await fetchConToken('auth/renew')
        const body = await resp.json()
        if(body.ok){
            localStorage.setItem('token', body.token)
            localStorage.setItem('token-init-date', new Date().getTime().toString())

            dispatch(login({
                uid:body.uid,
                name:body.name
            }))
        }else{
            dispatch(checkingFinish())
        }
    }
}

const checkingFinish = () => ({
    type: types.authCheckingFinish
})



//================================================================//
export const startLogout = () => {
    return ( dispatch: (arg0: { type: types; }) => void ) => {

        localStorage.clear();
        dispatch( tramiteLogout() );
        dispatch( logout() );
    }
}

const logout = () => ({ type: types.authLogout })