import { useDispatch, useSelector } from "react-redux"
import { Navigate } from "react-router"
import Swal from "sweetalert2"
import { startLogin } from "../../actions/auth"
import useForm from "../../hooks/useForm"
import { IRootState } from "../../interfaces/interfaces"

const Login = () => {

    const dispatch = useDispatch()
    const [formLoginValues, handleLoginInputChange] = useForm({
        email:'',
        password:''
    })
    const {email, password} = formLoginValues


    const handleLogin = (event:any) => {
        event.preventDefault()
        if(!email || !password) {
            Swal.fire('Oops', 'All fields must be complete.','warning')
        }else{
            dispatch(startLogin({email, password}))
        }
    }
    const {uid} = useSelector((state:IRootState)=>state.auth)
    if(uid){return <Navigate to="/home" />}
    return (
        <div className="login__form__container">
            <h1 className="section__title">Login Interface</h1>
            <form className="login__form grid">
                <div className="login__form__inputs grid">
                    <div className="login__form__content">
                        <label className="login__form__label">Email</label>
                        <input 
                            name="email" 
                            type="text" 
                            className="login__form__input"
                            autoComplete="off"
                            value={email}
                            onChange={handleLoginInputChange}
                        />
                    </div>
                    <div className="login__form__content">
                        <label className="login__form__label">Contrasea</label>
                        <input 
                            type="password" 
                            className="login__form__input"
                            name="password"
                            autoComplete="off"
                            value={password}
                            onChange={handleLoginInputChange}
                        />
                    </div>
                </div>
                <div className="login__form__btns">
                    <div>
                        <button className='button button__flex' onClick={handleLogin}>LOGIN</button>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default Login
