import { lazy } from "react";
import Login from "../components/login/Login";
import { Route } from "../interfaces/interfaces";

const LazyAppScreen = lazy(() => import('../components/app/AppScreen'));

export const routes:Route[] = [
    {
        to:'/',
        path:'/*',
        Component:LazyAppScreen,
        name:'Home'
    },
    {
        to:'/login',
        path:'/login',
        Component:Login,
        name:'Login'
    }
]

export const publicRoutes:Route[]=[
    {
        to:'/login',
        path:'/login',
        Component:Login,
        name:'Login'
    }
]

export const privateRoutes:Route[]=[
    {
        to:'/',
        path:'/*',
        Component:LazyAppScreen,
        name:'Home'
    }
]