import { ITramitesState } from "../interfaces/interfaces"
import { types } from "../types/types"
const init = {
    tramites: [],
    activeTramite: null,
    loading:false,
    tramitesPerDay:[]
}

export const tramitesReducer = (state:ITramitesState = init, action: any) => {
    switch (action.type) {
        case types.tramiteAddNew:
            return{
                ...state,
                tramites:[
                    action.payload,
                    ...state.tramites
                ]
            }
        case types.tramiteUpdate:
            return{
                ...state,
                tramites:state.tramites.map(t=>(t.id === action.payload.id) ? action.payload : t)
            }
        case types.tramiteDeleted:
            return{
                ...state,
                tramites:state.tramites.filter(t=>(t.id !== action.payload))
            }
        case types.tramiteSetActive:
            return{
                ...state,
                activeTramite: action.payload
            }
        
        case types.tramiteClearActive:
            return{
                ...state,
                activeTramite: null
            }
        case types.tramiteLoaded:
            return{
                ...state,
                tramites:[...action.payload],
                loading:false
            }
        case types.tramiteDayLoaded:
            return{
                ...state,
                tramitesPerDay:[...action.payload]
            }
        case types.tramiteLogout:
            return{
                tramites:[],
                activeTramite: null
            }
        case types.tramiteLoading:
            return{
                ...state,
                loading:!state.loading
            }
        default:
            return state
    }
}