import { types } from "../types/types"

const init = {
    checking:true
}

export const authReducer = (state = init, action:any) => {
    switch (action.type) {
        case types.authLogin:
            return {
                ...state,
                checking:false,
                ...action.payload
            }
        case types.authCheckingFinish:
            return{ 
                ...state,
                checking:false
            }
        case types.authLogout:
            return{
                checking:false
            }
        default:
            return state
    }    
}